.image-preload {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-size: cover;
  -webkit-filter: blur(5px);
          filter: blur(5px);
}

.image-loaded {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-size: cover;
  opacity: 0;
  transition: opacity 1s ease;
}

.image-fade-in {
  opacity: 1;
}

.image-hide {
  opacity: 0;
}

.image-container-native-color {
  background-color: #f6f7f8;
  position: relative;
  margin-bottom: 0.5rem;
  margin-top: 0.5rem;
  width: 100%;
  min-height: 250px;
  overflow: hidden;
}

@media only screen and (min-width: 400px) {
  .image-container-native-color {
    min-height: 320px;
  }
}

@media only screen and (min-width: 500px) {
  .image-container-native-color {
    min-height: 400px;
  }
}

.image-container-nocolor {
  position: relative;
  overflow: hidden;
  width: 250px;
  min-height: 535px;
}

.main {
  padding: 4.5rem 5%;
  display: flex;
  flex: 1 1;
}

.main.native-content {
  padding: 1em 5% 0 5%;
  flex-direction: column;
  margin-bottom: 1rem;
}

.text-main {
  text-align: left;
  width: 100%;
}

.title {
  margin: 0;
  font-size: 5em;
  font-weight: 700;
  color: #111;
  margin-bottom: 1rem;
}

.title.native-content {
  font-size: 3em;
}

.intro {
  color: #666;
  margin: 0;
  font-size: 2em;
  font-weight: 100;
  line-height: 1.4em;
  margin-bottom: 1rem;
}

.intro.native-content {
  color: #333;
  font-size: 1.26em;
}

.copy {
  font-size: 1em;
  font-weight: 300;
  margin: 0;
  line-height: 1.5rem;
  margin: 1rem 0 1rem 0;
}

.copy.native-content {
  line-height: 1.3 em;
}

.typewriter {
  display: inline-block; /* Makes sure cursor ends at end of text  */
}
.typewriter h2 {
  font-family: monospace;
  overflow: hidden; /* Ensures the content is not revealed until the animation */
  border-right: 0.1em solid #f40ef0; /* The typwriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  letter-spacing: 0.11em; /* Adjust as needed */
  -webkit-animation: typing 2s steps(50, end), blink-caret 1s step-end 6;
          animation: typing 2s steps(50, end), blink-caret 1s step-end 6;
}

/* The typing effect */
@-webkit-keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

/* The typewriter cursor effect */
@-webkit-keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #f40ef0;
  }
}
@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #f40ef0;
  }
}

